import React from 'react';
import { Panel, Group, RichCell, Avatar, Header, Title, ConfigProvider, Root, View, AdaptivityProvider, AppRoot } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import './styles.css';
import Icon24ChevronRight from '@vkontakte/icons/dist/24/chevron_right';

import ArtistEpicHeader from './components/ArtistEpicHeader';

import newRelease from './img/newRelease.png'
import converter from './img/converter.png'
import spotify from './img/2spotify.png'

class App extends React.Component {
  componentDidMount() {
    const schemeAttribute = document.createAttribute('scheme');
    if (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) schemeAttribute.value = 'space_gray';
    else schemeAttribute.value = 'bright_light';

    window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', e => {
      schemeAttribute.value = e.matches ? 'space_gray' : 'bright_light';
    });
    document.body.attributes.setNamedItem(schemeAttribute);
  }

  render () {
    return (
    <ConfigProvider>
     <AdaptivityProvider>
      <AppRoot>
       <Root activeView='home'>
        <View id='home' activePanel='home'>
         <Panel id='home'>
          <div style={{position: 'relative'}}>
            <ArtistEpicHeader src={'https://sun9-61.userapi.com/impf/29ZJ9DRJLtS_e9KPQHJJakgjt8MLpeBSseYxQQ/g5HjTVHi1JY.jpg?size=1620x2160&quality=96&proxy=1&sign=e2a47607476be0a08ebdbebf101b8f9c&type=album'}>Daniil Davydov</ArtistEpicHeader>
            <Title style={{color: '#fff', position: 'absolute', bottom: 14, fontSize: '2.5em', marginLeft: 18, maxWidth: '80%'}} weight="heavy">Daniil Davydov</Title>
          </div>
          <Group header={<Header>Проекты</Header>} mode='plain'>
           <RichCell
            id='newRelease'
            style={{cursor: 'pointer'}}
            after={<Icon24ChevronRight />}
			      before={<Avatar size={76} mode='image' src={newRelease} />}
            text="Сервис для отслеживания появления новых треков у Ваших любимых исполнителей."
            caption="25 млн пользователей"
            onClick={()=>{
              var link = document.createElement("a");
				      link.target = '_blank';
				      link.href = `https://vk.com/app7647010`;
				      link.click();
            }}
		       >Музыкальные новинки</RichCell>
		       <RichCell
            id='converter'
            style={{cursor: 'pointer'}}
            after={<Icon24ChevronRight />}
			      before={<Avatar style={{ background: '#3D3D3D' }} size={76} mode='image' src={converter} />}
            text="Сервис для безопасного экспорта фотографий разных расширений."
            caption="20 млн пользователей"
            onClick={()=>{
              var link = document.createElement("a");
				      link.target = '_blank';
				      link.href = `https://vk.com/app7561776`;
				      link.click();
            }}
		       >Конвертер</RichCell>
           <RichCell
            id='converter'
            style={{cursor: 'pointer'}}
            after={<Icon24ChevronRight />}
			      before={<Avatar style={{ background: '#3D3D3D' }} size={76} mode='image' src={spotify} />}
            text="Сервис для безопасного экспорта фотографий разных расширений."
            caption="2 млн пользователей"
            onClick={()=>{
              var link = document.createElement("a");
				      link.target = '_blank';
				      link.href = `https://dan0102dan.ru/2spotify`;
				      link.click();
            }}
		       >Перенос песен</RichCell>
		      </Group>
         </Panel>
        </View>
       </Root>
      </AppRoot>
     </AdaptivityProvider>
    </ConfigProvider>
    )
  }
}

export default App;