import React, { useEffect } from 'react'
import { useWindowScroll } from 'beautiful-react-hooks'
import { PanelHeader } from '@vkontakte/vkui'

import './styles.css'

const ArtistEpicHeader = ({ children, src, ...props }) => {
  const HeaderHeight = 355;

  useEffect(() => {
    if (window.scrollY > 0) {
      document.getElementById('artistsHeader').getElementsByClassName('PanelHeader__content')[0].style.color=`rgba(var(--header-text), var(--header-text), var(--header-text), ${window.scrollY/HeaderHeight})`
      document.getElementById('artistsHeader').getElementsByClassName('PanelHeader__in')[0].style.background=`rgba(var(--header-r), var(--header-g), var(--header-b), ${window.scrollY/HeaderHeight})`
    }
  });

  useWindowScroll(() => {
    document.getElementById('artistsHeader').getElementsByClassName('PanelHeader__content')[0].style.color=`rgba(var(--header-text), var(--header-text), var(--header-text), ${window.scrollY/HeaderHeight})`
    document.getElementById('artistsHeader').getElementsByClassName('PanelHeader__in')[0].style.background=`rgba(var(--header-r), var(--header-g), var(--header-b), ${window.scrollY/HeaderHeight})`
  });

  return (
    <>
      <PanelHeader
        {...props}
        id='artistsHeader'
        separator={false}
      >{children}</PanelHeader>
      <div id='ArtistPicture' style={{ width: '100%', height: '56vh', borderBottomLeftRadius: 25, borderBottomRightRadius: 25, marginTop: 'calc(-1vh - var(--panelheader_height_ios) - var(--safe-area-inset-top))', background: `linear-gradient(to bottom, rgba(65, 65, 65, .2), rgba(0, 0, 0, .9)), url(${src})`, backgroundRepeat: 'no-repeat', backgroundSize: 'cover', backgroundPosition: 'center' }} />
    </>
  )
}

export default ArtistEpicHeader